









export default {
	onLoad(e) {
		uni.showLoading({
			title: '加载中……',
			mask: false
		});
		console.log(e);
		const id = e.id;
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news/36kr/' + id,
			method: 'GET',
			data: {},
			success: res => {
				console.log(res.data);
				this.title = res.data.title;
				this.strings = res.data.content;
				uni.hideLoading();
			},
			fail: () => {},
			complete: () => {}
		});
	},
	data() {
		return {
			title: '',
			strings: ''
		};
	}
}
